#contactPage{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
}
.contactPageTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}
.contactDesc{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;

}
.contactForm{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
  
}
.Name,.email, .msg{
    font-size:medium;
    width: 100%;
    max-width:40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem ;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background:rgb(40,40,40);

}
.submitbtn{
    background: white;
    border: none;
    padding: 0.75rem 3.5rem;
    border-radius: 0.5rem;
    margin: 2rem;
}
.links{
display: flex;
flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 5rem;
    width: 5rem;
    margin: 0 0.75rem;
    cursor: pointer;
}
@media screen and (max-width:860px) {
    .contactDesc{
        font-size: small;
    }
    .Name,.email, .msg{
        display: flex;
        flex-wrap: wrap;
    }   
    
}