#intro{
    height: calc((100vh - 5rem));
    width: 100vw;
max-width: 75rem;
margin: 0 auto;
overflow: hidden;
position: relative;

}
.bg{
    position: absolute;
    z-index: -1;
    top: -4rem;
    right: 0;
    object-fit: cover;
    height: 100vh;

}
.introContent{
    height: 100vh;
    width: 100vw;
    padding: 3rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}
.hello{
    font-size: 1.5rem;
    font-weight: 200;

}
.introName{
    color: crimson;
}
.introPara{
    font-size: medium;
    font-weight: 100;
}
.btn{
    background: whitesmoke;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border:none;
    border-radius: 2rem;
    cursor: pointer;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.5rem;
    height: 1rem;
    cursor: pointer;
}
@media  screen and (max-width:840px) {
    .bg{
        right: -10vw;
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5rem;
    }
    
}
@media  screen and (max-width:480px) {
    .bg{
        right: -20vw;
    }
        
}
